@import "../../css/share.scss";
.home {
    position: relative;
    min-height: 100vh;
    background: #171932;
    &-title {
        height: px(730);
        background: #24243e url($imgUrl + "home_header_bg.png") no-repeat 0 0;
        background-size: 100%;
    }
    &-tab {
        position: relative;
        padding-bottom: px(93);
        background: #23233d;
        &__container {
            position: relative;
            top: px(-10);
            width: px(690);
            height: px(300);
            padding: px(36) px(26.1);
            border-radius: px(18);
            margin: 0 auto;
            box-sizing: border-box;
            background: linear-gradient( 0deg, rgba(253, 206, 160, 1) 0%, rgba(255, 247, 244, 1) 100%);
            &-button {
                display: flex;
                align-items: center;
                button {
                    display: inline-block;
                    width: px(319);
                    height: px(72);
                    border: none;
                    text-align: center;
                    font-size: px(30);
                    color: #ffffff;
                    font-family: PingFang SC;
                    font-weight: 500;
                    background: transparent;
                    &.leftButton {
                        border-right: none !important;
                        border-radius: px(12) 0px 0px px(12);
                        &.active {
                            background: linear-gradient(270deg, #6c5042 0%, #44362e 100%);
                        }
                    }
                    &.rightButton {
                        border-left: none !important;
                        border-radius: 0 px(12) px(12) 0;
                        &.active {
                            background: linear-gradient(270deg, #44362e 0%, #6c5042 100%);
                        }
                    }
                    &.unActive {
                        color: #332016;
                        border: 1px solid #322016;
                    }
                }
            }
            &-tag {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-around;
                &>button {
                    display: inline-block;
                    min-width: px(139);
                    height: px(52);
                    border-radius: px(26);
                    border: px(2) solid #f3725a;
                    color: #f3725a;
                    text-align: center;
                    margin-top: px(26);
                    background: transparent;
                    &.active {
                        color: #ffffff;
                        background: #f3725a;
                    }
                }
            }
        }
    }
    &-dapp {
        position: relative;
        min-height: px(400);
        &__developing {
            position: relative;
            top: px(-39);
            width: px(490);
            min-height: px(100);
            margin: 0 auto;
            padding: px(19) 0 px(15);
            background: url($imgUrl + "dapp_dev_info_bg.png") no-repeat 0 0;
            background-size: 100%;
            p {
                color: #332016;
                text-align: center;
                font-weight: 500;
                margin: 0;
                &:nth-of-type(1) {
                    font-size: px(29);
                }
                &:nth-of-type(2) {
                    margin-top: px(14);
                    font-size: px(24);
                }
            }
        }
        &__list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-top: px(11);
            padding: 0 px(30);
            box-sizing: border-box;
            &>a {
                display: block;
                outline: 0;
                padding: 0;
                margin: 0;
                text-decoration: none;
                background: red;
            }
            .home-dapp__card {
                position: relative;
                width: px(313);
                height: px(390);
                border-radius: px(18);
                padding-bottom: px(18);
                margin-bottom: px(57);
                background: #ffffff;
                overflow: hidden;
                &-logo {
                    display: inline-block;
                    width: 100%;
                    height: px(204);
                    border: 0;
                    outline: 0;
                    border-radius: px(16) px(16) 0 0;
                }
                &-info {
                    padding: px(12) px(19) px(30);
                    // box-shadow: 0 -1px 0 0 #24243e;
                    span {
                        display: inline-block;
                        width: 100%;
                        font-size: px(28);
                        font-weight: 500;
                        &.dapp-name {
                            color: #332016;
                            @include ellipsis;
                        }
                        &.dapp-price {
                            color: #d3421c;
                            margin-top: px(10);
                        }
                    }
                }
                &-countDown {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 110%;
                    height: px(52);
                    line-height: px(52);
                    text-align: center;
                    color: #ffffff;
                    font-size: px(23);
                    font-weight: 500;
                    background: rgba(0, 0, 0, 0.32);
                    border-radius: px(18) 0 0 0 px(18);
                    z-index: 100;
                }
                &>button {
                    display: block;
                    width: px(184);
                    height: px(50);
                    background: linear-gradient( -90deg, rgba(253, 208, 164, 1) 0%, rgba(255, 235, 219, 1) 100%);
                    border-radius: px(24);
                    border: none;
                    outline: 0;
                    color: #d3421c;
                    margin: 0 auto;
                    &.dsb {
                        background: #c8c8c8;
                        color: #ffffff;
                    }
                }
            }
        }
    }
    .no-data {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: px(375);
        padding-bottom: px(35);
        &>img {
            display: block;
            width: px(325);
            height: px(242);
            border: none;
            outline: 0;
            margin-bottom: px(81);
        }
        &>span {
            display: block;
            width: 100%;
            text-align: center;
            font-size: px(38);
            color: #c1c1c1;
        }
    }
    .go-history {
        position: fixed;
        top: px(136);
        right: -1px;
        display: inline-block;
        width: px(161);
        height: px(51);
        line-height: px(51);
        padding-left: px(23);
        color: #ffffff;
        font-size: px(28);
        box-sizing: border-box;
        background: linear-gradient(270deg, #c28555 0%, #dfaa75 100%);
        border-radius: px(28) 0 0 px(28);
        z-index: 1000;
    }
}
